$width-day: 100px;
$width-month: $width-day * 7;
$gap: 1px;

$start-day:
  "mon" 0,
  "tue" #{($gap + $width-day) * 1},
  "wed" #{($gap + $width-day) * 2},
  "thu" #{($gap + $width-day) * 3},
  "fri" #{($gap + $width-day) * 4},
  "sat" #{($gap + $width-day) * 5},
  "sun" #{($gap + $width-day) * 6};
